import React from "react";
import { Link } from "react-router-dom";
import logo from "../assets/images/logo.png";

const headerLinks = [
  {
    name: "curriculum",
    route: "/curriculum",
  },
  {
    name: "history",
    route: "/history",
  },

  {
    name: "instructors",
    route: "/instructors",
  },
  {
    name: "NEWS & EVENTS",
    route: "/news-events",
  },
  {
    name: "SIGN-UP",
    route: "/signup",
  },
  {
    name: "FAQS",
    route: "/faqs",
  },
  {
    name: "NYBA MEMBERS",
    route: "/nyba-members",
  },
  {
    name: "RSR MEMBERS",
    route: "/rsr-members",
  },

  {
    name: "DONATE",
    route: "/donate",
  },

  {
    name: "contact",
    route: "/contact",
  },
  {
    name: "admin",
    route: "/admin",
  },
];

export default function Header() {
  return (
    <nav className="navbar navbar-expand-lg p-3 pb-1 ">
      <div className="container-lg p-0">
        <div className=" border-lines bt w-100">
          <div className="navbar" id="navbarSupportedContent">
            <Link to="/" className="navbar-brand m-0">
              {" "}
              <img src={logo} alt="" width="auto" height="45" />
            </Link>
            <div className=" me-auto"></div>
            <div className="d-flex align-items-center">
              <button className="btn py-1 btn-custom rounded-pill">
                Donate
              </button>
              <div className="nav-item dropdown dropstart ms-3">
                <span
                  className="nav-link  p-0 d-flex align-items-center"
                  id="navbarDropdown"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  <span className="material-icons hamburger-icon">
                    &#xe5d2;
                  </span>
                </span>
                <ul
                  className="dropdown-menu dropdown-menu-custom"
                  aria-labelledby="navbarDropdown"
                >
                  {headerLinks.map((link,index) => {
                    return (
                      <li key={index}>
                        <Link
                          className="dropdown-item dropdown-item-custom"
                          to={link.route}
                        >
                          {link.name}
                        </Link>
                      </li>
                    );
                  })}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </nav>
  );
}
