        <div className="accordion-item">
          <h2 className="accordion-header">
            <button
              className="accordion-button"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#faq-3"
              aria-expanded="true"
              aria-controls="faq-3"
            >
              REISHIKI（礼式）– BOWING IN/OUT OF CLASS
            </button>
          </h2>
          <div
            id="faq-3"
            className="accordion-collapse collapse "
            data-bs-parent="#faqs"
          >
            <div className="accordion-body">
              <p className="text-small ">
                {" "}
                Sword training begins and ends with courtesy. Reishiki is
                translated as formal etiquette.
                <br /> For us it includes the formal courtesy ceremony that
                starts and ends each class.
                <br /> ……………………………………………………………………………… <br />
                (Begin from standing position sitting into seiza (left knee
                followed by right knee.)
                <br />
                <br /> Sitting in seiza rest both hands at your upper hip (align
                yourself with the most senior student).
                <br />
                <br /> Place the sword in front of you and then gently place it
                on to your right side,
                <br /> cutting edge facing inward with the tsuba aligned with
                your right knee.
                <br />
                <br /> The most senior member (other than the instructor) calls
                the Rei. “Shinzen ni Rei” (Courtesy to the art you are
                studying.)
                <br /> Note: Wait for the lead instructor to initiate all the
                actions of bowing
                <br />
                <br /> Begin the bow by placing the left hand down first,
                followed by the right hand creating a diamond shape. Then bow
                keeping
                <br /> your back straight. Return right hand then left hand back
                to hips. These actions should be performed slowly and precisely.
                <br />
                <br /> “Sensei ni Rei” (Courtesy to the teacher.) Followed by a
                bow.
                <br />
                <br /> “Otani Sensei ni Rei” – showing respect for the founder
                of NYBA Yoshiteru Otani Sensei.
                <br /> NOTE: This is only done in NYBA class (not during RSR
                seminars).
                <br />
                <br /> “Otagai ni Rei” (Mutual gratitude – please honor me with
                your practice)
                <br /> the class responds by bowing and saying – “Onagai
                Shimasu”
                <br /> At the conclusion of class the response is – “Arigato
                Gozaimashita”.
                <br />
                <br /> With the right hand stand your sword directly in front of
                you (centered between your knees at arm’s length) with the
                cutting
                <br /> edge facing toward you. Straighten the sageo by pulling
                down on it. Then place the sword with tsuka to the left and edge
                <br /> toward you and using one motion push the sageo in towards
                the saya.
                <br /> “To ni Rei” (Courtesy to the sword.) Followed by a bow.
                <br />
                <br /> With your right hand grab the sageo, moving your hand to
                the left and picking up the sword while holding the tsuba.
                <br /> Return the sword to the standing position directly in
                front of you, holding with both hands. Note: Wait for the lead
                instructor
                <br /> to initiate the action.
                <br />
                <br /> With your right hand pull the sageo down with a snapping
                motion and immediately place the saya into the center of your
                <br /> stomach. Place the sword in between one of the layers of
                the obi and through the left side of your hakama (feeding it
                <br /> under one of the himo.)
                <br /> Once sword is in place, take the sageo with right hand
                and simply tuck it over
                <br /> one of the himo on the right side and then tuck it into
                the right side of the hakama.
                <br />
                <br /> NOTE: If a student is late, please bow in on the side
                ﬁrst before joining the class. This can also be done standing.
              </p>
            </div>
          </div>
        </div>
