import React from "react";
import ReCAPTCHA from "react-google-recaptcha";
export default function Contact() {
  const [contactData, setContactData] = React.useState({
    firstname: "",
    lastname: "",
    email: "",
    subject: "",
    message: "",
    reCaptcha: false,
  });
  function onChange() {
    setContactData({...contactData,reCaptcha:true})
  }
  function handleSubmit(event){
    event.preventDefault();
    console.log(contactData)
    setContactData({
      firstname: "",
      lastname: "",
      email: "",
      subject: "",
      message: "",
      reCaptcha: false,
    })
  }
  return (
    <div>
      <h1 className="home-main-heading ps-1">Contact</h1>
      <div className="border-top-line mx-2">
        <div className="row g-4 py-3">
          <div className="col-12 col-lg-6">
            <p className="text-semi-lg mb-3">
              Please fill out the form below and we will contact you shortly.
            </p>
            <form onSubmit={handleSubmit} className="needs-validation" >
              <div className="mb-3">
                <label htmlFor="firstname" className="form-label custom-form-label">
                  First Name <span className="text-danger">(Required)</span>
                </label>
                <input
                  type="text"
                  required={true}
                  aria-required="true"
                  className="form-control form-control-lg"
                  id="firstname"
                  onChange={(event) =>
                    setContactData({
                      ...contactData,
                      firstname: event.target.value,
                    })
                  }
                />
              </div>
              <div className="mb-3">
                <label htmlFor="lastname" className="form-label custom-form-label">
                  Last Name <span className="text-danger">(Required)</span>
                </label>
                <input
                  type="text"
                  required={true}
                  aria-required="true"
                  className="form-control form-control-lg"
                  id="lastname"
                  onChange={(event) =>
                    setContactData({
                      ...contactData,
                      lastname: event.target.value,
                    })
                  }
                />
              </div>
              <div className="mb-3">
                <label htmlFor="email" className="form-label custom-form-label">
                  Email address <span className="text-danger">(Required)</span>
                </label>
                <input
                  type="email"
                  required={true}
                  aria-required="true"
                  className="form-control form-control-lg"
                  id="email"
                  onChange={(event) =>
                    setContactData({
                      ...contactData,
                      email: event.target.value,
                    })
                  }
                />
                <div className="invalid-feedback">Please select a valid state.</div>
              </div>
              <div className="mb-3">
                <label htmlFor="subject" className="form-label custom-form-label">
                  subject
                </label>
                <input
                  type="text"
                  className="form-control form-control-lg"
                  id="subject"
                  onChange={(event) =>
                    setContactData({
                      ...contactData,
                      subject: event.target.value,
                    })
                  }
                />
              </div>
              <div className="mb-3">
                <label htmlFor="message" className="form-label custom-form-label">
                  Message
                </label>
                <textarea
                  className="form-control"
                  id="message"
                  rows="18"
                  cols="10"
                  onChange={(event) =>
                    setContactData({
                      ...contactData,
                      message: event.target.value,
                    })
                  }
                ></textarea>
              </div>
              <ReCAPTCHA
                sitekey="6LfHrmsbAAAAACL-MIqQaO7eFdQ1_LtLIHxVXBye"
                onChange={onChange}
                onExpired = {()=>setContactData({
                  ...contactData,
                  reCaptcha:false
                })}
              />
              {!contactData.reCaptcha && <p>Please fill the CAPTCHA</p>}

              <button type="submit" className="send-button mt-3">
                Send
              </button>
            </form>
          </div>
          <div className="col-12 col-lg-6">
            <h5 className="font-weight-high text-default mb-3">
              New York Budo Association
            </h5>
            <p className="m-0 text-semi-lg">
              Next Gen Studios* <br /> 519 8th Ave
              <br /> 12th Floor
              <br /> (check video monitor to confirm)
              <br /> New York, NY 10018
              <br />
              <br /> newyorkbudoassociation@gmail.com
              <br />
              <br /> *NOTE: Classes are held at this address,
              <br /> however, this is not our mailing address.
              <br />
              <br /> For all other inquiries:
              <br />
              <br /> New York Budo Association
              <br /> 299 Clermont Ave.
              <br /> Brooklyn, NY 11205
            </p>
            <div className="map border-lines py-4 mt-4">
              <iframe
              title="nyba-location"
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3022.3948516121063!2d-73.99507538469979!3d40.75333947932744!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89c259adab5d38ad%3A0x37f3a69d51f1746e!2sNew%20York%20Budo%20Association!5e0!3m2!1sen!2s!4v1624557142245!5m2!1sen!2s"
                width="100%"
                height="430"
                style={{ border: 0 }}
                allowFullScreen={true}
                loading="lazy"
              ></iframe>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
