import React from "react";
import {  Route } from "react-router-dom";
import Layout from '../layout/Layout' 

export default function RouteWrapper({ component: Component, ...rest }) {
  return (
    <Route
    exact
      {...rest}
      render={(props) => (
        <Layout {...props}>
          <Component {...props} />
        </Layout>
      )}
    />
  );
}
